.navigationBar {
  font: 1rem montserrat;
  font-weight: 300;
  background-color: rgba(0, 127, 127, 0.95);
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  width: 100%;
}

.navigationBar .navbar-brand {
  color: white;
  font-weight: 900;
}

.navigationBar .navbar-nav .nav-link {
  color: white;
}

.navigationBar .navbar-nav .nav-link.active {
  color: rgb(255, 236, 69);
}

/* Highlight icons on hover */
.navigationBar .nav-link:hover .MuiSvgIcon-root {
  color: rgb(219, 165, 34);
}

.navigationBar .navbar-brand:hover {
  color: rgb(219, 165, 34);
}

.navigationBar .navbar-nav .nav-link:hover {
  color: rgb(219, 165, 34);
}

.navigationBar .navbar-nav .nav-link.active:hover {
  color: rgb(219, 165, 34);
}

.navbar-toggler {
  font-size: 1rem;
  background-color: white;
  margin: 0 1rem;
}

.navbar-toggler:hover {
  background-color: rgb(219, 165, 34);
}

@media (min-width: 768px) {
  .navbar-collapse {
    order: 0;
  }
}
